<template>
  <div class="proj-list">
    <div class="content-container">
      <div class="header-btn-area">
        <router-link to="/admin/addProject">
          <el-button type="primary" icon="el-icon-plus"></el-button>
        </router-link>
      </div>
      <el-form :inline="true" label-width="100px">
        <el-form-item label=":">
            <el-input v-model="projname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="success" @click.prevent="search"></el-button>
        </el-form-item>
      </el-form>
      <el-table :data="projList">
        <!-- <el-table-column type="index"></el-table-column> -->
        <el-table-column label="">
          <template slot-scope="scope">{{ scope.row.id }}</template>
        </el-table-column>
        <el-table-column label="logo">
          <template slot-scope="scope">
            <img class="table-image" :src="scope.row.logo_url" alt="">
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <a class="link" :href="'/projDetail/info/' + scope.row.id" target="_blank">{{ scope.row.name_cn }}</a>
          </template>
        </el-table-column>
        <el-table-column label="" prop="created_at"></el-table-column>
        <el-table-column label="" width="235px">
          <template slot-scope="scope">
            <router-link :to="'/admin/projDepositBox/' + scope.row.id">
              <el-button size="mini"></el-button>
            </router-link>
            <router-link :to="'/admin/editProject/' + scope.row.id">
              <el-button size="mini"></el-button>
            </router-link>
            <el-button size="mini" type="danger" @click="showDel(scope.row.id)"></el-button>
            <el-button style="margin-left: 0px; margin-top: 5px;" v-if="scope.row.status===0" size="mini" type="primary" @click="authProject(scope.row.id)"></el-button>
            <el-button style="margin-left: 0px; margin-top: 5px;" v-if="scope.row.status===1" size="mini" type="warning" @click="clearAuth(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

    <!--Pagination-->
    <el-pagination class="footer-page-box" @size-change="onSizeChange" @current-change="onCurChange" :current-page="pageno" :page-sizes="[10, 20, 30, 40]" :page-size="perpage" layout="total, sizes, prev, pager, next, jumper" :total="dataCount">
    </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      projList: [],
      pageno: 1,
      perpage: 10,
      dataCount: 0,
      user: [],
      projname: ''
    }
  },
  mounted () {
    this.updateData({countPages: true})
    this.getUser()
  },
  methods: {
    updateData (params) {
      this.$http.post('/api/getProjBasicList', {
        pageno: this.pageno,
        perpage: this.perpage,
        projname: this.projname
      }).then((res) => {
        var resData = res.data
        if (resData.errcode === 0) {
          this.projList = resData.data.dataList
          //  countPages
          if (params && params.countPages) {
            this.dataCount = resData.data.dataCount
          }
        }
      })
    },
    getUser () {
      this.$http.post('/api/getUser').then((res) => {
        if (res.data.errcode === 0) {
          this.user = res.data.data
          // console.log(this.user)
        }
      })
    },
    showDel (projId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delProject(projId)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    delProject (projId) {
      this.$http.post('/api/delProject', {
        projId: projId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.updateData()
        }
      })
    },
    authProject (projId) {
      this.$http.post('/api/authProject', {
        projId: projId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.updateData()
        }
      })
    },
    clearAuth (projId) {
      this.$http.post('/api/clearProjAuth', {
        projId: projId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.updateData()
        }
      })
    },
    onSizeChange (perpage) {
      this.perpage = perpage
      this.updateData()
    },
    onCurChange (pageno) {
      this.pageno = pageno
      this.updateData()
    },
    search () {
      if (this.pageno > 1) {
        this.pageno = 1
      }
      this.updateData({countPages: true})
    }
  }
}
</script>

<style lang="scss" scoped>
.content-container {
  padding: 20px;
}
</style>



